import wPicker from "@/components/w-picker/w-picker.vue";

var wxCharts = require("../../../utils/wxcharts.js");
var _self;
var Charts;

var Data = {
	categories: ['5/1', '5/2', '5/3', '5/4', '5/5', '5/6', '5/7', '5/8', '5/9', '5/10', '5/11', '5/12', '5/13', '5/14', '5/15'],
	series: [{
			name: 'AQI',
			data: [396, 423, 418, 363, 370, 360, 350, 423, 418, 363, 370, 360, 350, 418, 363, 370]
		}
	]
};
var width;
export default {
	components:{
		wPicker
	},
	
	onLoad: function() {
		uni.setNavigationBarTitle({
			title: this.getNowFormatMonth()+'市空气质量'
		});
		
		uni.getSystemInfo({
			success(res) {
				width = res.screenWidth - 10;
			}
		})
	},
	data() {
		return {
			title: 'Hello',
			sdate: this.getNowFormatMonth(),
			tabList:[{
				mode:"yearMonth",
				name:"年月",
				value:[this.getNowYear(),this.getNowMonth()] //年月在列表的序号
			}],
			tabIndex:0
		}
	},
	computed:{
		mode(){
			return this.tabList[this.tabIndex].mode
		},
		defaultVal(){
			return this.tabList[this.tabIndex].value
		}
	},
	onReady: function() {
		this.ShowCharts("charts", Data);
		//this.hideLoading();
	},
	methods: {
		toggleTab(index){
			this.tabIndex=index;
			this.$refs.picker.show();
		},
		onConfirm(val){
			//当前所选择的日期
			this.sdate = val.result;
			console.log(val.result);
			
			uni.setNavigationBarTitle({
			title: val.result+'市空气质量'
		   });
		},
		/*显示图表*/
		ShowCharts: function(canvasId, data) {
			Charts = new wxCharts({
				canvasId: canvasId,
				type: 'line',
				legend: true,
				fontSize: 11,
				background: '#FFFFFF',
				animation: true,
				categories: data.categories,
				series: data.series,
				width: width,
				height: 280,
				pixelRatio:1,
			});
		},
		
		goDetail:function(id,storeName){			
			let detail = {
					id: id,
					storeName:storeName,
					date:this.sdate
				}
				uni.navigateTo({
					url: "daydata02?detail=" + encodeURIComponent(JSON.stringify(detail))
				})
		},
		
		getNowFormatMonth: function() {
			var date = new Date();
			var seperator1 = "-";
			var year = date.getFullYear();
			var month = date.getMonth() + 1;
			if (month >= 1 && month <= 9) {
				month = "0" + month;
			}
			var currentdate = year + seperator1 + month ;
			return currentdate;
		},
		
		getNowYear: function() {
			var date = new Date();
			var year = date.getFullYear();
			var currentdate = year -2018 ;
			return currentdate;
		},
		
		getNowMonth: function() {
			var date = new Date();
			var month = date.getMonth() + 1;
			var currentdate = month - 1;
			return currentdate;
		},
	}
}
